.brand-color{
  color: $brand-color;
}
.highlight {
  color: $brand-color;
}

.brand-redline {
  color: $brand-redline;
}

.bg-skewed {
  background: linear-gradient(
     -10deg,
     transparent 30%,
     $beach-sand 70%
     );
}
.bg-gray {
  background-color: #f5f5f5;
}
.bg-soft {
  background-color: #FFF9ED;
}
.bg-light{
  background-color: #f9f9fb;
}
.bg-ghost {
  background-color: ghostwhite;
}
.bg-smokey {
  background-color: whitesmoke;
}
.bg-beachy {
  background-color: $beach-sand;
}
.bg-skyblue {
  background-color: $baby-slate;
}
.bg-peachy {
  background-color: $baby-brand;
}
.bg-slate {
  background-color: $slate-blue;
}
.bg-dark {
    background: $slate-blue;
    color: $baby-slate;
}
.bg-cloudy {
    background: #dfe6e9;
}

.sandy {
  background-color: $beach-sand-light;
}
.skyblue {
  background-color: $baby-slate;
}
.stormy {
  background-color: #f8f8f8;
}
.rounded-box {
  padding: 2em;
  border-radius: 15px;
}

.label-orange {
    background-color: $brand-color;
    color: $baby-brand;
}
.label-blue {
    background-color: $slate-blue;
    color: $baby-slate;
}
.text-yt {
  color: #fff;
}
.text-bb {
  color: $baby-slate;
}
