.svg-icon {
    display: inline-flex;
    align-self: center;
}
.svg-icon svg,
.icon img {
    height: 1em;
    width: 1em;
    fill: currentColor;
}
.svg-icon.baseline svg {
  height: 6em;
  width: 6em;
}
.svg-icon.baseline svg,
.icon img {
    top: .125em;
    position: relative;
}
.svg-icon img {
    height: 120px;
    width: 120px;
}
.svg-icon.baseline-xl svg {
  height: 10em;
  width: 10em;
}

.svg-icon.baseline-jumbo svg {
  height: 15em;
  width: 15em;
}

