@media print {
  * {
    background: transparent !important;
    color: black;
    text-shadow: none !important;
    filter: none !important;
    -ms-filter: none !important;
  }
  body {
    font-size: 11pt;
    line-height: 1.3;
    font-family: Arial, sans-serif;
  }
  a, a:visited {
    text-decoration: underline;
    word-wrap: break-word;
  }
  a[href^="http://"]:after,
  a[href^="https://"]:after {
    content: " (" attr(href) ")";
  }
  a[href^="#"]:after {
    content: "";
  }
  p, h2, h3 {
    orphans: 3;
    widows: 3;
  }
  h2, h3 {
    page-break-after: avoid;
  }
  img {
    max-width: 100%;
  }
  @page {
    margin: 0.3cm;
  }
  footer,
  header,
  .notification-clock,
  #contact-form,
  #fb-root {
    display: none;
  }
  .product .container,
  .product .container-fluid {
    display: none;
  }
  .product .print-header {
    display: block;
    position: relative;
  }
  .product .print-estimate {
    position: absolute;
    top: 20px;
    right: 40px;
  }
  .product .print-estimate h4 {
    font-size: 24pt;
    font-weight: bolder;
  }
  .product .print-contact {
    position: absolute;
    top: 40px;
    left: 40px;
  }
  .product .print-contact img {
    width: 120px;
    padding-bottom: 10px;
  }
  .product .print-contact p {
    font-size: 11pt;
  }
  .product .print-fineprint {
    position: absolute;
    left: 40px;
    top: 980px;
  }
  .product .print-fineprint p {
    font-size: 10pt;
    font-weight: bold;
  }
  .cd-builder-steps .summary-list {
    text-align: center;
    margin-top: 160px;
  }
  .cd-builder-steps .summary-list .product-preview {
    width: 100px;
    display: block;
    margin-top: 40px;
  }
  .cd-builder-steps .summary-list .summary-accessories li {
    margin: 0;
  }
  .cd-builder-steps .summary-list li {
    margin: 0;
    padding: 0;
  }
  .cd-builder-steps .summary-list h3 {
    font-size: 16pt;
    padding-top: 0.5em;
  }
  .cd-builder-steps .summary-list h2 {
    font-size: 12pt;
    padding: 0;
    margin-bottom: 5px;
    color: #444444;
  }
}

.gradient-background {
  background: linear-gradient(300deg, #ffe9d5, #fffed5, #ff83a4);
  background-size: 180% 180%;
  animation: gradient-animation 18s ease infinite;
}

@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
